@import "https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap";
@import "https://fonts.googleapis.com/css2?family=Titillium Web";
* {
  -webkit-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
}

html {
  --white: white;
  --bg: #f8f9fa;
  --fg: #333;
  --primary-selected: #9d67ef;
  --primary: #8542eb;
  --primary-low: rgba(133, 66, 235, .44);
  --secondary: #4bc7ef;
  --success: #5ace84;
  --input: #f6f2fd;
  --yellow: #f9ba37;
  --second-yellow: #ffd262;
  --second-yellow-selected: rgba(255, 210, 98, .66);
  --gray: #555;
  --new-gray: #9c9494;
  --second-new-grey: #ebe7f5;
  --light-gray: #ccc;
  --shadow: #e6e6e6;
  background-color: var(--bg);
  --publicity-height: 130px;
  color: var(--fg);
  background: #5e8c01 url("newFarmBackground.1d62f42c.png") top / 100% no-repeat;
  font-family: Titillium Web, sans-serif;
  font-size: 13px;
  font-weight: 500;
}

.focused {
  outline-style: none;
  outline-color: (--primary);
  box-shadow: var(--primary-low) 0px 0px 0px 2px;
  border-radius: 6px;
  outline-width: 1px;
}

#logo:focus {
  outline-style: none;
  outline-color: (--primary);
  box-shadow: none;
  outline-width: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  max-width: 117rem;
  margin: 0 auto;
  line-height: 1;
  overflow-y: auto;
}

.publicity-container {
  height: var(--publicity-height);
  background: radial-gradient(var(--primary-selected), var(--primary));
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.publicity-container img {
  aspect-ratio: 1;
  height: 40%;
  margin: 10px;
}

.publicity-container .farm-img {
  transform: translateY(-4px);
}

.publicity-container .learn-img {
  transform: translateY(-2px);
}

.publicity-container span {
  text-shadow: 1px 0 #fd941f, -1px 0 #fd941f, 0 1px #fd941f, 0 -1px #fd941f, -1px -1px #fd941f, 1px -1px #fd941f, 1px 1px #fd941f, -1px 1px #fd941f, -1px 1px #fd941f;
  color: var(--bg);
  letter-spacing: 3px;
  font-size: 4rem;
  font-weight: normal;
  font-family: Bubblegum Sans !important;
}

.close-publicity {
  color: var(--bg);
  position: absolute;
  top: 12px;
  right: 12px;
}

.publicity-decoration {
  top: var(--publicity-height);
  z-index: 0;
  background: linear-gradient(180deg, var(--primary), transparent);
  width: 100%;
  height: 30px;
  position: absolute;
  left: 0;
}

.publicity-decoration.no-publicity-position {
  top: 0;
}

header {
  flex-flow: row;
  align-items: center;
  max-height: 59px;
  margin: 164px 15px 0;
  display: flex;
}

main {
  margin-top: 15px;
}

header.no-publicity-position {
  margin: 33px 15px 0;
}

.header-extension {
  display: flex;
}

h1 {
  color: #272d36;
  font-size: 40px;
  font-weight: normal;
  line-height: 40px;
}

h2 img {
  vertical-align: middle;
  border-radius: 50%;
  justify-content: center;
  width: 35px;
  height: 35px;
  padding: 5px;
  box-shadow: 0 2px 14px rgba(0, 0, 0, .19);
}

h3 {
  color: #333;
  align-self: flex-start;
  margin: 0;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  display: flex;
}

h4 {
  text-align: center;
  color: #333;
  margin-top: .25em;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
}

h5 {
  color: #333;
  align-items: center;
  margin-top: .3em;
  margin-bottom: 0;
  font-size: 13px;
  font-weight: normal;
  line-height: 16px;
