  font-size: 13px;
  font-weight: 400;
  line-height: 18px;
}

header div.title {
  white-space: break-spaces;
  flex: 15%;
  margin-left: 18px;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
}

nav {
  justify-content: space-between;
