  text-align: center;
  border-radius: 27px;
  justify-content: center;
  width: 129px;
  height: 42px;
  margin-top: 10px;
  font-family: Titillium Web, sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  display: flex;
  position: relative;
}

.btn-liquid .inner {
  z-index: 2;
