}

.btn-liquid canvas {
  z-index: 1;
  position: absolute;
  top: -50px;
  bottom: -50px;
  left: -50px;
  right: -50px;
  transform: translate(13px, 23px);
}

.user-info.selected {
  color: var(--fg);
  font-weight: bold;
}

.pool-meta.actions {
  justify-content: center;
}

.pool-meta.actions button {
  min-width: 48px;
  padding: 1rem;
}

.stake-action {
  fill: #fff;
}

.user-info span {
  box-sizing: content-box;
  margin-top: 15px;
  font-size: 14px;
  font-weight: 600;
  line-height: 11px;
