}

.narwallets-logo {
  background-image: url("narwallets-logo.37085d70.svg");
}

article, form, .popup, .flex-column {
  flex-flow: column;
  align-items: center;
  display: flex;
}

article, form, .box {
  background: #fff;
  border-radius: 12px;
  padding: 10px;
  box-shadow: 0 0 20px rgba(220, 226, 235, .7);
}

.input-group-box {
