  top: .2em;
}

.tooltip .tooltip-text {
  text-align: center;
  visibility: hidden;
  z-index: 30;
  word-break: break-word;
  white-space: break-spaces;
  border-radius: 6px;
  width: 24em;
  max-width: 50vw;
  padding: 5px;
