input[type="checkbox"][disabled] {
  outline: 1px solid #00f;
}

input#terms-of-use {
  background-color: green;
}

input[type="checkbox"]#terms-of-use {
  display: none;
}

.disclaimer {
  margin-top: 10px;
}

.disclaimer label:before {
  cursor: pointer;
  background: green;
  border: 1px solid #035f8f;
  width: 18px;
  height: 18px;
  display: inline-block;
}

input[type="checkbox"]#terms-of-use + label:before {
  content: "";
  color: #00f;
  text-align: center;
  background: #00f;
  border-color: #00f;
  font-size: 15px;
  line-height: 15px;
}

input[type="checkbox"]#terms-of-use:disabled + label:before {
  color: #00f;
  background: #ddd;
  border-color: #ccc;
}

input[type="checkbox"]#terms-of-use:checked + label:before {
  content: "✓";
}

.max {
  color: #00f;
  font-size: 14px;
}

#near-balance a {
