}

#stake-form-not-connected {
  max-width: 320px;
  margin: 0 auto;
}

.loader {
  position: realative;
  background-image: url("cheddar.3357e5b0.svg");
  border: 16px solid #f3f3f3;
  border-top-color: #fd941f;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin: 10% auto;
  animation: 2s linear infinite spin;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
